.dasboard
{
	.header, .stats
	{
		.user-details, .listing
		{
			padding-left: 0px;
		}
	}

	.header
	{
		padding-top: 40px;

		.profile-img
		{
			height: 70px;
			width: 70px;
			border-radius:0;
			position: relative;
			box-shadow: 0px 0px 0px 0px #ccc;
			margin-top:8px;

			.inearprofile
			{
				img
				{
					height: 70px;
					width: 70px;
				}
			}
		}
		.user-details
		{
			.follows 
			{
				li
				{
					padding-right: 30px;
					a
					{
						font-size: 16px;
					}
				}
			}

			.user-name
			{
				font-size: 18px;
			}
		}

		.usermedal
		{
			margin-top: 0px;

			ul
			{
				text-align: center;

				li
				{
					width: 70px;
				}
			}
		}
	}

	.stats
    {	
    	display: none;
        .listing
        {
            ul
            {
                li
                {
                    padding-right: 10px;
                    a
                    {
                        font-size: 14px;
                    }
                }
            }
        }
    }
 }



 .profile-body-container
 {
 	.left-links
 	{
	 	position: fixed;
	    width: 100%;
	    z-index: 12;
	    bottom: 0;
		/*box-shadow: 0px 0px 20px 0px #a1a1a1;*/
        
        padding:0;
		

 		.stylish-links
        {
        	


        	ul
        	{
        		width: 100%;

        		&:before
        		{
                    content:"";
                    position: absolute;
                    height: 0px;
                    width: 100%;
        		}

        		li
        		{
        			float:left;
        			padding:0;
        			width: 20%;
        			background: #fff;


        			a
        			{

	        			div
	        			{
	        				height:50px;
	        				width: 50px;
	        				line-height: 50px;
	        			}	

                        &:hover, &.active
                        {
                            div
                            {
                                background: $insta_red;
                            }

                            &.active:before
                            {
                                content: "\f0d8";
                                position: absolute;
                                font-family: FontAwesome;
                                color: $insta_red;
                                margin: -27px 0px 0px -9px;
    							font-size: 34px;
                            }
                        }


        			}
        		}
        	}
        }
 	}

	.right-body-part
	{
		margin-top: 0px;
		min-height: inherit;

		.body-part
		{
			.data-center
			{
				.my-seat
				{
					h2{}
				}
			}
		}
	}
 }


.btnn
{

    a
    {
        padding:4px 10px 6px 10px;
    }
}




.search-result
{
	.grid
    {

        .media
        {
            width: 100%;
            float: none;
            border-bottom: 0px;

            &:nth-child(odd)
            {
                border-right: solid 0px $instaborder;
            }

            .media-left
            {
                width: 100%;
                display: block;
                padding-right: 0;


                .media-object
                {
                    width: 100%;
                    height: auto;
                }
            }

            .media-body
            {
                width: 100%;
                display: block;
                margin-top:30px;

                ul
                {

                    li
                    {
                        
                        a
                        {
                           
                            span
                            {
                                display: none;
                            }

                            i
                            {
                                opacity: 1;
                                visibility: visible;

                                &.fa-tag,
                                &.fa-flag
                                {
                                   opacity: 1;
                                   visibility: visible;
                                }   
                            }
                        }
                    }
                }
            }
        }
    }

}

.news-page
{

	.slide 
	{
		margin-bottom: 15px;
	}

    .latest-news
    {
    	margin-top:0;

		.news-listed
        {
            padding: 0;
            margin-bottom: 15px;

            &:nth-child(even),
            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3)
            {
                padding-left: 0px;
            }

            &:nth-last-child(2),
            &:nth-last-child(3)
            {
                .figure
                {
                        /*border-bottom: 0;*/
                }
            }
        }
    }
}

